/* ------------------------------------------
   PROJECT CARD COMPONENT
------------------------------------------ */
function ProjectCard({ project, onClick }) {
  return (
    <div
      className="project-card"
      onClick={onClick}
      role="button"
      tabIndex={0}
      onKeyDown={(e) => (e.key === "Enter" ? onClick() : null)}
      style={{
        "--bg-image": `url(${project.banner_image})`,
        "--neutral-bg-opacity": project.neutral_bg_opacity,
        "--hover-overlay-color": project.hover_overlay_color,
        "--text-color": project.text_color,
      }}
      aria-label={`Open project ${project.title}`}
    >
      <div className="card-content">
        <img
          src={project.logo_path}
          alt={`${project.title} logo`}
          className="project-logo"
        />
        <h3 className="card-title">{project.title}</h3>
      </div>

      <div className="project-overlay" aria-hidden="true">
        <p className="overlay-primary">{project.descriptionPrimary}</p>
      </div>

      <div className="project-tech-icons">
        {project.tech_icons?.map((icon, i) => (
          <img key={i} src={icon} alt="tech-icon" className="tech-icon" />
        ))}
      </div>
    </div>
  );
}

/* ------------------------------------------
   MAIN COMPONENT
------------------------------------------ */
export default function Projects(props) {
  const [selectedProjectId, setSelectedProjectId] = useState(null);
  const detailRef = useRef(null);
  const gridRef = useRef(null);

  const selectedProject = projects.find((p) => p.id === selectedProjectId) || null;

  function openProject(id) {
    setSelectedProjectId(id);
  }

  function closeProject() {
    setSelectedProjectId(null);
    if (gridRef.current) gridRef.current.focus({ preventScroll: true });
  }

  useEffect(() => {
    if (selectedProject && detailRef.current) {
      detailRef.current.scrollIntoView({ behavior: "smooth", block: "start" });
      detailRef.current.focus({ preventScroll: true });
    }
  }, [selectedProject]);

  return (
    <div className="projects-main">
      <Header theme={props.theme} />

      <Fade bottom duration={2000} distance="40px">
        <div className="projects-heading-div">
          <h1
            className="projects-heading-text"
            style={{ color: props.theme.text }}
          >
            {projectsHeader.title}
          </h1>
          <p
            className="projects-header-detail-text subTitle"
            style={{ color: props.theme.secondaryText }}
          >
            {projectsHeader.description}
          </p>
        </div>
      </Fade>

      <div
        className="projects-body-div"
        aria-live="polite"
        ref={gridRef}
        tabIndex={-1}
      >
        {projects.map((proj) => (
          <ProjectCard
            key={proj.id}
            project={proj}
            onClick={() => openProject(proj.id)}
          />
        ))}
      </div>

      {selectedProject && (
        <section
          className="project-detail-panel"
          style={{
            "--banner-image": `url(${selectedProject.banner_image})`,
            "--detail-overlay-color": selectedProject.detail_overlay_color,
            color: selectedProject.text_color || undefined,
          }}
          aria-label={`${selectedProject.title} details`}
          tabIndex={-1}
          ref={detailRef}
        >
          <div className="detail-inner">
            <img
              className="detail-logo"
              src={selectedProject.logo_path}
              alt={`${selectedProject.title} logo`}
            />

            <div className="detail-header">
              <h2 className="detail-title">{selectedProject.title}</h2>
              <p className="detail-subtitle">{selectedProject.subtitle}</p>
            </div>

            <div className="detail-meta">
              <div className="tags" aria-hidden="false">
                {selectedProject.tags?.map((t, i) => (
                  <span key={i} className="tag" title={t}>
                    {t}
                  </span>
                ))}
              </div>

              <div className="role" aria-label="Project role">
                <span className="role-item"> {selectedProject.role}</span>
              </div>
            </div>

            <div className="detail-description-secondary">
              <ul>
                {selectedProject.descriptionSecondary.map((point, i) => (
                  <li key={i}>{point}</li>
                ))}
              </ul>
            </div>

            <div className="detail-tech-icons">
              {selectedProject.tech_icons?.map((icon, i) => (
                <img key={i} src={icon} alt={`tech-${i}`} />
              ))}
            </div>

            <div className="detail-actions">
              {selectedProject.project_link && (
                <a
                  className="view-project-btn"
                  href={selectedProject.project_link}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  View Project
                </a>
              )}

              <button className="close-detail-btn" onClick={closeProject}>
                Close
              </button>
            </div>
          </div>
        </section>
      )}

      <Footer theme={props.theme} onToggle={props.onToggle} />
      <TopButton theme={props.theme} />
    </div>
  );
}
